footer {
  background-color: rgba(var(--bg-d),1.0);

}


.footer {
  padding: 30px 0;

  &__inner{
  display: grid;
  grid-template-columns: auto 1fr auto;
  align-items: center;
  grid-gap: Min(5.747vw, 80px);


  @include small-tablet{
    grid-gap: 10px;
  }

  @include mobile{
    grid-template-columns: 100%;
    grid-gap: 24px;
    justify-items: center;
  }
  }

  @include mobile{
    padding: 40px 0;
  }


  &__deskr{
    text-align: center;
    color: rgba(var(--cl-a),1.0);
    font-family: var(--font-family);
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
    
    @include ios{
      max-width: 60%;
    }
  }

  &__company{
    @include small-tablet{
      display: flex;
      svg{
      width: 144px;
      height: 31px;
    }

    @include mobile{
      padding-top: 12px;
      position: relative;
      justify-content: center;
      width: 100%;
      &::before{
        position: absolute;
        content: '';
        left: 0;
        top: -5px;
        width: 100%;
        height: 1px;
        background-color: rgba(var(--bg-a),.6);
      }
    }

    


   
    
    
  }
  }

  &__logo{
    @include small-tablet{
      width: 131px;
      height: 60px;
    }
    @include mobile{
      width: 171px;
      height: 60px;
    }
  }

  
}
