header {
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 80;
}

.header {
  &-box {
    padding: 14px;
    display: grid;
    grid-template-columns: auto 1fr auto;
    grid-gap: clamp(20px, 7.04vw, 98px);
    align-items: center;
  }

  &.sticky{
    background-color: rgba(var(--bg-d),.9);
    box-shadow: 0px 4px 6px 0px rgba(0, 0, 0, 0.10);
  }
}
