}

.panel,
[data-content] {
  z-index: 20;
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.2s;
}

[data-tab-content], [data-inner-content], [data-small-content], [data-wrapper-content] {
  display: none !important;
}
[data-tab-content].active, [data-inner-content].active, [data-small-content].active, [data-wrapper-content].active {
  display: block !important;
}

.dis-scroll {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  overflow: hidden;
  background-attachment: fixed;
  overscroll-behavior: none !important;
  scroll-behavior: none !important;
}

.a-start {
  align-items: flex-start !important;
  padding-top: 0 !important;
  height: calc(100% - var(--header-height)) !important;
  top: calc(var(--header-height)) !important;
  transition: none !important;
}

.page--ios .dis-scroll {
  position: relative;
}

.overlay {
  position: fixed;
  height: 100%;
  right: 0;
  bottom: 0;
  left: 0;
  top: 0;
  z-index: 1;
  padding: 20px 20px;
  overflow-y: scroll;
  display: grid;
  justify-content: center;
  align-items: center;
  grid-template-rows: 1fr;
  visibility: hidden;
  opacity: 0;
  background: rgba(var(--bg-b), 0.36);
  transition: all 0.6s;
  transition-delay: 0.5s;
  pointer-events: none;
}
.overlay::-webkit-scrollbar {
  display: none;
}
.overlay.none {
  display: none !important;
}
.overlay.active {
  position: fixed;
  z-index: 20;
  visibility: visible;
  opacity: 1;
  transition: all 0.6s;
  pointer-events: initial;
}

*::-webkit-scrollbar {
  width: 7px;
  height: 7px;
  border-radius: 5px;
  cursor: pointer;
}
@media (max-width: 767px) {
  *::-webkit-scrollbar {
    display: none;
  }
}
*::-webkit-scrollbar-thumb {
  background-color: var(--scrollbar-thumb);
  cursor: pointer;
  transition: all 0.2s;
}
*::-webkit-scrollbar-track {
  background-color: var(--scrollbar-track);
}
*::-webkit-scrollbar-button {
  display: none;
  background-color: transparent;
  box-shadow: none;
}

fieldset {
  margin: 0;
  padding: 0;
  border: none;
}

.modal {
  position: relative;
  left: 50%;
  transform: translate(-50%, 0vh);
  opacity: 0;
  grid-row: 1;
  grid-column: 1;
  transition: opacity 1s;
  pointer-events: none;
}
.modal.active {
  position: relative;
  transform: translate(-50%, 0);
  opacity: 1;
  z-index: 20;
  transition: opacity 1s;
  pointer-events: initial;
}
.modal__close {
  position: absolute;
  right: 19px;
  top: 20px;
  z-index: 20;
}
.modal .close svg {
  pointer-events: none;
  stroke: var(--svg-fill-d);
  transition: all 0.4s;
}
.modal .close:hover svg {
  stroke: var(--svg-fill-e);
}

