.animation-text {
  -webkit-font-smoothing: antialiased;
  --line: #bbc1e1;
  --line-active: #275efe;
  color: rgba(var(--cl-a), 1);
  font-family: var(--font-family);
  font-size: clamp(15px, 1.724vw, 24px);
  font-style: normal;
  font-weight: 500;
  line-height: 150%;
  margin: 0;
}
.animation-text__link {
  display: inline-block;
  position: relative;
  text-decoration: none;
  color: inherit;
  margin: 0 var(--spacing, 0px);
  transition: margin 0.25s, color 0.4s;
}
.animation-text__link:hover {
  --spacing: 15px;
  --stroke: var(--line-active);
  --stroke-delay: 0.1s;
  --offset: 180px;
  color: rgba(var(--cl-f), 1);
}
.animation-text__link svg {
  width: 200px;
  height: 90px;
  position: absolute;
  left: 50%;
  bottom: 0;
  transform: translate(-49%, 21px) translateZ(0);
  fill: none;
  stroke: var(--stroke, var(--line));
  stroke-linecap: round;
  stroke-dasharray: var(--offset, 69px) 278px;
  stroke-dashoffset: 361px;
  transition: stroke 0.25s ease var(--stroke-delay, 0s), stroke-dasharray 0.35s;
}
.animation-text * {
  box-sizing: inherit;
}
.animation-text *:before,
.animation-text *:after {
  box-sizing: inherit;
}
.animation-text br {
  display: none;
}

@keyframes flash {
  0% {
    background-position: left;
  }
  100% {
    background-position: right;
  }
}
.main-button {
  display: flex;
  width: 203px;
  height: 55px;
  justify-content: center;
  align-items: center;
  border-radius: 4px;
  color: rgba(var(--cl-a), 1);
  text-align: center;
  font-family: var(--font-family);
  font-size: 15px;
  font-style: normal;
  font-weight: 500;
  line-height: 150%;
  background: linear-gradient(90deg, #1E47B0, purple, #1E47B0);
  background-size: 300%;
  background-position: left;
  transition: all 0.5s;
}
.main-button:hover {
  background-position: right;
}

.callback {
  position: absolute;
  z-index: 20;
  right: 0;
  top: 42px;
  width: clamp(60px, 5.747vw, 80px);
  height: clamp(60px, 5.747vw, 80px);
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(var(--bg-c), 1);
  border-radius: 50%;
  filter: drop-shadow(0px 2px 30px rgba(0, 0, 0, 0.02));
  transition: all 0.4s;
}
.callback svg {
  fill: var(--svg-fill-a);
  transition: all 0.4s;
}
.callback:hover svg {
  fill: var(--svg-fill-b);
}
.callback::before, .callback::after {
  position: absolute;
  content: "";
  border-radius: 50%;
  inset: 0;
  width: clamp(60px, 5.747vw, 80px);
  height: clamp(60px, 5.747vw, 80px);
}
.callback::before {
  z-index: -1;
  background-color: rgba(var(--bg-c), 0.4);
  animation: 1.3s infinite alternate-reverse flashSecond;
}
.callback::after {
  z-index: -2;
  background-color: rgba(var(--bg-c), 0.3);
  animation: 1.3s infinite alternate-reverse flash;
}

@keyframes flash {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(1.35);
  }
}
@keyframes flashSecond {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(1.15);
  }
}
